}
.debug-colliding {
  width: 32px;
  height: 32px;
  position: relative;
  top: -32px; }

.number {
  text-align: center;
  vertical-align: middle;
  position: absolute;
  font-size: .9rem;
  line-height: .9rem;
  padding: 0 2px;
  opacity: 0; }

.number.top {
  top: -17px;
  left: 9.5px; }

.number.bottom {
  top: 2px;
  left: 9.5px; }

.number.left {
  top: -7.5px;
  left: 0px; }

.number.right {
  top: -7.5px;
  left: 19px; }

.active {
  background-color: deeppink;
  opacity: 1; }

.title {
  color: var(--primary-color); }

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--dark-color); }

.tile {
  width: 0;
  height: 0; }
  .tile img {
    border: solid 1px transparent;
    z-index: 0;
    width: 30px;
    height: 30px; }
    .tile img:hover {
      border: solid 1px black;
      z-index: 99999; }

.container {
  display: flex; }

.preview {
  width: 128px;
  height: 128px;
  margin-bottom: 2rem; }

.selected {
  border: 4px solid black; }

.image-list {
  display: flex;
  flex-direction: column; }
  .image-list .image {
    width: 32px;
    height: 32px;
    padding: 0.5rem;
    border: none; }
    .image-list .image:hover {
      background-color: #ddd; }
    .image-list .image.selected {
      background-color: black; }

h1 {
  font-size: 1.6rem; }

/* React app is injected into this container (I think) */
#root {
  height: 100%; }

/* container added for the css variable injection */
#root > div {
  height: 100%; }

/* react-hotkeys adds this container, doesn't have any className to grab onto */
div[tabindex="-1"] {
  height: 100%;
  display: flex; }

div[tabindex="-1"]:focus {
  outline: 0; }

.left, .right {
  display: flex;
  flex-direction: column; }

.left {
  flex-grow: 1;
  background-color: var(--dark-color);
  overflow-x: hidden;
  border: 1px solid var(--dark-color); }
  .left::-webkit-scrollbar {
    width: 1em; }
  .left::-webkit-scrollbar-thumb {
    background-color: var(--primary-color);
    border-radius: 10px; }

@media only screen and (max-width: 900px) {
  /* react-hotkeys container */
  div[tabindex="-1"] {
    /* The default 100% height was killing the layout */
    height: auto;
    background-color: var(--primary-color); } }

header {
  display: flex;
  padding: 1rem 1rem 1rem 1rem;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  color: var(--primary-color); }
  header button {
    margin-right: 1rem; }
  header button:last-child {
    margin-right: 0; }

.left header {
  justify-content: space-between; }

h1.logo {
  flex-grow: 1;
  margin: 0;
  margin-top: -5px;
  line-height: 1.3rem; }

.code, .game-container {
  display: flex;
  flex-direction: column; }

.code .rule:hover {
  background-color: var(--hover-color); }

.code span.comment {
  opacity: 0.5; }

.error {
  color: red;
  padding: 2rem; }

.code {
  background-color: var(--dark-color);
  font-size: 1.0rem;
  font-family: 'Fira Code';
  padding: 1rem 2rem;
  color: var(--primary-color); }

.code-sprite {
  -webkit-transform: scale(0.6);
          transform: scale(0.6);
  margin-left: .4rem;
  margin-right: .2rem; }

.game-container {
  padding: 1rem;
  padding-top: 0;
  flex-direction: column; }

